 border-box;\n}\n\n.container {\n  padding-right: 15px;\n  padding-left: 15px;\n  margin-right: auto;\n  margin-left: auto;\n  \n  @include breakpoint(sm) {\n    width: 750px;\n  }\n  \n  @include breakpoint(md) {\n    width: 970px;\n  }\n}\n\n.pull-left {\n  float: left !important;\n}\n\n.pull-right {\n  float: right !important;\n}\n\n.img-responsive {\n  display: block;\n  max-width: 100%;\n  height: auto;\n}","%clearfix {\n    *zoom: 1;\n    &:before, \n    &:after {\n        content: \" \";\n        display: table;\n    }\n    &:after {\n        clear: both;\n    }\n}\n//border box\n@mixin border-box {\n    -webkit-box-sizing: border-box;\n    -moz-box-sizing: border-box;\n    box-sizing: border-box; \n    }\n//retina display\n@mixin image-2x($image, $width, $height) {\n    @media (min--moz-device-pixel-ratio: 1.3),\n           (-o-min-device-pixel-ratio: 2.6/2),\n           (-webkit-min-device-pixel-ratio: 1.3),\n           (min-device-pixel-ratio: 1.3),\n           (min-resolution: 1.3dppx) {\n      /* on retina, use image that's scaled by 2 */\n      background-image: url($image);\n      background-size: $width $height;\n    }\n  }\n  // Mixin Breakpoints - Bootstrap Values\n@mixin breakpoint($class) {\n    \n    @if $class == 320 {\n      @media (max-width: 320px) { @content; }\n    }\n  \n    @else if $class == max350 {\n      @media (max-width: 350px) { @content; }\n    }\n  \n    @else if $class == max480 {\n      @media (max-width: 480px) { @content; }\n    }\n  \n    @else if $class == min480 {\n      @media (min-width: 480px) { @content; }\n    }\n  \n    @else if $class == xxs {\n      @media (max-width: 500px) { @content; }\n    }\n  \n    @else if $class == xs {\n      @media (max-width: 767px) { @content; }\n    }\n  \n    @else if $class == sm {\n      @media (min-width: 768px) { @content; }\n    }\n  \n    @else if $class == smd {\n      @media (max-width: 992px) { @content; }\n    }\n  \n    @else if $class == md {\n      @media (min-width: 992px) { @content; }\n    }\n  \n    @else if $class == lg {\n      @media (min-width: 1200px) { @content; }\n    }\n  \n    @else {\n      @warn \"Breakpoint mixin supports: xs, sm, md, lg\";\n    }\n  }\n\n  ","@import 'mixins';\n\n/* =========\n * Header\n * ========= */\n .header{\n    position: relative;\n    top: 0;\n    left: 0;\n    width: 100%;\n    z-index: 9999;\n    background: #42b549;\n    height: 50px;\n    padding: 5px 0;\n\n    .top-navigation-menu {\n      float: right;\n      margin: 0 20px;\n\n      @include breakpoint(xs) {\n        margin-right: 0;\n      }\n\n      ul {\n        margin: 0;\n        padding: 0;\n        list-style: none;\n\n        li {\n          float: left;\n        }\n      }\n\n      &__link {\n        text-decoration: none;\n        border-radius: 4px;\n        color: #FFF;\n        padding: 6px 12px;\n        margin-top: 4px;\n        display: block;\n        letter-spacing: 1px;\n        font-weight: 500;\n        -webkit-transition: 0.2s;\n        transition: 0.2s;\n      }\n    }\n  }\n  .logo{\n    float: left;\n    margin-top: 8px;\n\n    @include breakpoint(xs) {\n      float: none;\n      text-align: center;\n    }\n  \n    & img {\n      height: 25px;\n    }\n  }\n  .overlay {\n    background: #000;\n    position: fixed;\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n    opacity: .7;\n    z-index: 200;\n    display: none;\n  }\n  \n  /* -------------\n   * Pivot\n   * ------------- */\n  .wrap_bridge-menu {\n    margin-top: 9px;\n\n    @include breakpoint(xs) {\n      display: none;\n    }\n  }\n  .bridge-widget {\n    position: relative;\n    display: inline-block;\n    box-sizing: border-box;\n  }\n  .bridge-widget.active .bw-container {\n    display: block;\n    height: auto;\n  }\n  .bw-container::before {\n    top: -22px;\n    right: 21px;\n    border-width: 11px;\n    border-color: transparent transparent #FFFFFF;\n  }\n  .bw-container::before {\n    position: absolute;\n    display: block;\n    border-style: solid;\n    content: '';\n  }\n  .bw-button {\n    position: relative;\n    z-index: 999;\n    display: block;\n    width: 27px;\n    height: 27px;\n    border: 0;\n    outline: 0;\n    top: -2px;\n    left: 1px;\n    background-color: transparent;\n    background-image: url(https://ecs7.tokopedia.net/assets/images/ic_menu.png);\n    background-repeat: no-repeat;\n    text-indent: -9999px;\n    background-size: 27px 27px;\n    cursor: pointer;\n    -webkit-transition: opacity .25s ease;\n    transition: opacity .25s ease;\n  }\n  .bw-button:hover {\n    opacity: .8;\n  }\n  .bw-container {\n    position: absolute;\n    z-index: 1000;\n    display: none;\n    box-sizing: border-box;\n    padding: 20px;\n    border: 1px solid #d5d5d5;\n    border-radius: 3px;\n    background-color: #fff;\n    width: 250px;\n    top: 36px;\n    right: -20px;\n  }\n  .bw-item {\n    margin-bottom: 3px;\n    width: 104px;\n    height: 104px;\n    display: block;\n    float: left;\n    text-align: center;\n  }\n  .bw-item a {\n    text-decoration: none;\n  }\n  .bw-icon__wrapper {\n    box-sizing: border-box;\n    margin: 0 auto;\n    padding: 15px 0 0;\n    width: 70px;\n    height: 70px;\n    background: #FFF;\n    transition: background .25s ease;\n  }\n  .bw-item:hover {\n    -webkit-box-shadow: 0 0 5px 2px rgba(204,204,204,.48);\n    -moz-box-shadow: 0 0 5px 2px rgba(204,204,204,.48);\n    box-shadow: 0 0 5px 2px rgba(204,204,204,.48);\n  }\n  .bw-icon__text {\n    display: block;\n    margin: 0;\n    padding: 0 0 20px;\n    color: #787878;\n    font-size: 11px;\n    line-height: 1.5;\n  }\n  .bw-icon {\n    width: 50px;\n    height: 50px;\n    margin: 0 auto;\n    background-position: center;\n    background-size: 37px;\n    background-repeat: no-repeat;\n  }\n  .bw-icon-pulsa {\n    background-image: url(https://ecs7.tokopedia.net/img/icon_tokopedia-pulsa@2x.png);\n    background-size: 45px;\n  }\n  .bw-icon-tiket {\n    background-image: url(https://ecs7.tokopedia.net/img/icon_tokopedia-tiket@2x.png);\n    background-size: 30px;\n  }\n  .bw-icon-toped {\n    background-image: url(https://ecs7.tokopedia.net/img/icon_tokopedia-toped@2x.png);\n  }\n  .bw-icon-official {\n    background-image: url(https://ecs7.tokopedia.net/img/icon_tokopedia-official-store@2x.png);\n    background-size: 45px;\n  }\n  .bw-icon-help {\n    background-image: url(https://ecs7.tokopedia.net/img/icon_tokopedia-bantuan@2x.png);\n    background-size: 38px;\n  }\n  .bw-icon-donasi {\n    background-image: url(https://ecs7.tokopedia.net/img/icon_tokopedia-donasi@2x.png);\n    background-size: 45px;\n  }\n  ","/* ==========\n * Footer\n * ========== */\n .footer {\n    padding: 20px 15px;\n    background-color: #FFF;\n    box-shadow: 0 -1px 1px 0 rgba(0, 0, 0, 0.05);\n  }\n  \n  .footer-copyright {\n    padding: 10px;\n    @include breakpoint(smd) {\n        float: none !important;\n        text-align: center;\n    }\n  }\n  \n  .footer-connect {\n    padding: 5px;\n    @include breakpoint(smd) {\n        float: none !important;\n        text-align: center;\n    }\n  }\n  \n  .footer-connect > ul {\n    margin: 0;\n    padding: 0 10px;\n    display: inline-block;\n    height: 30px;\n    @include breakpoint(smd) {\n        display: block;\n        margin: auto;\n    }\n  }\n  \n  .footer-connect > ul:first-child {\n    border-right: 1px solid #CDCDCD;\n    @include breakpoint(smd) {\n        margin-bottom: 10px;\n        border-right: none;\n    }\n  }\n  \n  .footer-connect > ul > li {\n    list-style: none;\n    display: inline-block;\n    padding: 0 6px;\n    @include breakpoint(smd) {\n        padding: 0 2px;\n    }\n  }\n  \n  .footer-connect__apps {\n    height: 30px;\n    @include breakpoint(smd) {\n        height: 27px;\n    }\n  }\n  \n  .footer-connect__socmed {\n    height: 30px;\n    @include breakpoint(smd) {\n        height: 27px;\n    }\n  }\n  ","@import 'variables';\n@import 'mixins';\n@import 'base';\n\n@import 'part/header';\n@import 'part/footer';\n\n/* ===================\n * Container\n * =================== */\n.tp-container {\n  display: none;\n  max-width: 768px;\n\n  &.active {\n    display: block;\n  }\n}\n\n/* ===================\n * Jumbotron\n * =================== */\n.tp-jumbotron {\n  margin: 0;\n  width: 100%;\n  height: 240px;\n  background: url(https://ecs7.tokopedia.net/tokopoints/info/img-tokopoints-header-bg.jpg) center no-repeat;\n  background-size: cover;\n  position: relative;\n  padding: 30px 0;\n  text-align: center;\n  \n  @include breakpoint(xs) {\n    height: auto;\n  }\n\n  &-text {\n    color: #FFF;\n    margin-top: 35px;\n    z-index: 10;\n    position: relative;\n    \n    @include breakpoint(xs) {\n      margin-top: 0;\n    }\n\n    &__h1 {\n      text-shadow: 2px 2px rgba(0, 0, 0, 0.2);\n      font-size: (36rem/14);\n      font-weight: 300;\n      margin: 10px 0;\n      line-height: 1.5;\n\n      @include breakpoint(xs) {\n        font-size: 1.6rem;\n      }\n    }\n    \n    &__p {\n      font-size: 1rem;\n      font-weight: 600;\n      line-height: 1.5rem;\n      letter-spacing: 8px;\n      padding-left: 8px;\n      margin: 30px 0 10px;\n      color: rgba(0, 0, 0, .38);\n\n      @include breakpoint(xs) {\n        font-size: 0.85rem;\n      }\n    }\n  }\n\n  .container {\n    position: relative;\n    \n    &:before {\n      content: \" \";\n      display: block;\n      background: url(https://ecs7.tokopedia.net/tokopoints/info/img-tokped-logo.png) center no-repeat;\n      background-size: contain;\n      width: 143px;\n      height: 144px;\n      position: absolute;\n      top: 50%;\n      left: 0;\n      transform: translateY(-40%);\n      z-index: 0;\n\n      @include breakpoint(xs) {\n        display: none;\n        width: 68px;\n        height: 82px;\n      }\n    }\n\n    &:after {\n      content: \" \";\n      display: block;\n      background: url(https://ecs7.tokopedia.net/tokopoints/info/img-tokopoints-treasure.png) center no-repeat;\n      background-size: contain;\n      width: 221px;\n      height: 185px;\n      position: absolute;\n      top: 50%;\n      right: 0;\n      transform: translateY(-40%);\n      z-index: 0;\n\n      @include breakpoint(xs) {\n        display: none;\n        width: 85px;\n        height: 82px;\n      }\n    }\n  }\n}\n\n/* ===================\n * Info accordion\n * =================== */\n.main {\n  padding: 40px 0 50px;\n  \n  &__h3 {\n    margin-top: 0;\n    margin-bottom: 40px;\n    font-size: 24px;\n    text-align: center;\n    font-weight: 600;\n  }\n}\n\n.info {\n  margin: 20px 0;\n\n  &-accordion {\n    border-bottom: $color-border-dark;\n\n    &-panel {\n      color: $color-black-secondary;\n\n      &.expanded {\n        & .panel-header:after {\n          transform: rotate(180deg);\n        }\n        \n        & .panel-header:before {\n          content: \" \";\n          position: absolute;\n          bottom: 0;\n          left: 0;\n          display: block;\n          height: 2px;\n          width: 20px;\n          background: $color-green;\n        }\n      }\n\n      & .panel-header {\n        font-size: 1rem;\n        color: $color-black-primary;\n        font-weight: 600;\n        line-height: 1.5;\n        position: relative;\n        padding: 16px 40px 16px 0;\n        border-top: 1px solid $color-border-dark;\n        cursor: pointer;\n        \n        &:after {\n          content: \" \";\n          background: url(https://ecs7.tokopedia.net/assets/images/arrow/arrow-down-green.svg) center no-repeat;\n          display: block;\n          height: 16px;\n          width: 16px;\n          position: absolute;\n          top: 50%;\n          right: 16px;\n          margin-top: -7px;\n          transition: .3s;\n        }\n      }\n\n      & .panel-body {\n        display: none;\n        padding: 10px 0;\n        color: $color-black-secondary;\n\n        @include breakpoint(xs) {\n          width: 100vw;\n          margin-left: -15px;\n          padding: 15px;\n        }\n\n        & ol, ul {\n          padding-left: 20px;\n          \n          & li {\n            margin-bottom: 20px;\n\n            & img {\n              margin-bottom: 10px;\n            }\n          }\n        }\n\n        & p {\n          margin-bottom: 20px;\n          line-height: 1.6;\n        }\n\n        & table {\n          border-collapse: collapse;\n          \n          th {\n            padding: 5px;\n\n            &.text-left {\n              text-align: left;\n            }\n          }\n\n          td {\n            padding: 5px;\n            border-top: 1px solid #E0E0E0;\n\n            &.text-center {\n              text-align: center;\n            }\n          }\n        }\n\n        & a {\n          text-decoration: none;\n          color: #42B549;\n          transition: .2s;\n\n          &:hover {\n            color: #008000;\n          }\n        }\n\n        & .img-info {\n          max-width: 250px;\n        }\n      }\n    }\n  }\n\n  &__p {\n    line-height: 1.5;\n    color: $color-black-secondary;\n  }\n\n  &__h3 {\n    color: $color-black-primary;\n    font-size: 1.6rem;\n    margin: 0 0 10px;\n\n    @include breakpoint(xs) {\n      font-size: 1.2rem;\n    }\n  }\n}\n","//background\n$green:rgb(64, 169, 49);\n// grid variables\n$grid-columns: 12;\n$grid-max-width: 65em;\n// Base color\n$color-green: #42b549;\n$color-green-secondary: #f0fcf1;\n$color-orange: #ff5722;\n$color-blue: #46d5e0;\n$color-yellow: #ffb200;\n$color-yellow-2: #ffc107;\n$color-tint: #fafdcd;\n$color-grey: #efefef;\n$color-dark-grey: #979797;\n$color-red: #e94f4f;\n$color-red-light: #ea212d;\n$color-red-error: #d50000;\n$color-green-soft: #cde4c3;\n$color-yellow-soft: #ecdb77;\n$color-border-grey: #e0e0e0;\n$color-border-green: #cde4c3;\n$color-orange-soft: #ff974f;\n$color-dark-green: #008000;\n$color-dark-red: #b94a48;\n$color-white-hex-1: #fafafa;\n$color-white-hex-2: #f1f1f1;\n$color-white-hex-3: #f3fef3;\n$color-white-hex-4: #f8f8f8;\n\n// Unify color\n$color-black-primary: rgba(0, 0, 0, 0.7);\n$color-black-secondary: rgba(0, 0, 0, 0.54);\n$color-black-disabled: rgba(0, 0, 0, 0.38);\n$color-black-disabled2: rgba(0, 0, 0, 0.42);\n$color-placeholder: rgba(0, 0, 0, 0.26);\n\n$color-white-primary: rgba(255, 255, 255, 1);\n$color-white-secondary: rgba(255, 255, 255, 0.7);\n$color-white-disabled: rgba(255, 255, 255, 0.5);\n\n$color-grey-0: rgba(0, 0, 0, 0.025);\n$color-grey-1: rgba(0, 0, 0, 0.05);\n$color-grey-2: rgba(0, 0, 0, 0.1);\n$color-grey-3: rgba(0, 0, 0, 0.2);\n$color-grey-4: rgba(248, 248, 248, 1);\n$color-grey-5: rgba(241, 241, 241, 1);\n\n// Overlay Color\n$color-overlay: rgba(0, 0, 0, 0.25);\n$color-overlay-2: rgba(0, 0, 0, 0.75);\n\n// Border color\n$color-border-dark: rgba(0, 0, 0, 0.12);\n\n// Box shadow color\n$box-shadow-normal: 1px 1px 2px 0 rgba(0, 0, 0, 0.1);\n\n$color-grey-disabled: rgba(0, 0, 0, 0.12);\n$color-grey-disabled-2: rgba(0, 0, 0, 0.26);\n$color-grey-disabled-3: rgba(0, 0, 0, 0.38);\n// breakpoints\n$breakpoint-small: \"only screen and (min-width: 20em)\";\n$breakpoint-medium: \"only screen and (min-width: 48em)\";"],"sourceRoot":""}]);

